import Vue from 'vue'
import Router from 'vue-router'
import CalculatorTyumen from '@/components/CalculatorTyumen'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'CalculatorTyumen',
      component: CalculatorTyumen
    }
  ]
})



