<template>
  <div>
    <h1>Расчет платы за вывоз мусора в Тюмени</h1>
    <div class="calculatorBlock">
      <div>
      <h2>Если считать по количеству жильцов (так будем платить с 2019 года)</h2>
      <label class="calculatorLabel" for="">Количество проживающих в квартире</label>
      <input class="calculatorInput" type="number" v-model="value">
      </div>
      <div class="calculatorPrice">
      <animated-number
          :value="value"
          :formatValue="formatToPrice"
          :duration="duration"
        />
      </div>
    </div>
    <div class="calculatorBlock">
      <div>
      <h2>Если считать по квадратным метрам жилья</h2>
      <label class="calculatorLabel" for="">Площадь вашего жилья, м²</label>
      <input class="calculatorInput" type="number" v-model="valueArea">
      </div>
      <div class="twoPrices">
        <div>
          <div class="calculatorPrice">
        <animated-number
            :value="valueArea"
            :formatValue="formatToPriceArea"
            :duration="duration"
          />
        </div>
          <div class="subheader">
         (тариф был предложен ранее)
        </div>
        </div>
        <div>
          <div class="calculatorPrice">
        <animated-number
            :value="valueArea"
            :formatValue="formatToPriceAreaOld"
            :duration="duration"
          />
        </div>
          <div class="subheader">
         (платили до 2019 года)
        </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AnimatedNumber from "animated-number-vue";

export default {
  components: {
    AnimatedNumber
  },
  data() {
    return {
      value: 1,
      valueArea: 35,
      duration: 300
    };
  },
  methods: {
    formatToPrice(value) {
      return `${Number(value * 139.06).toFixed(2)} руб.`;
    },
    formatToPriceArea(valueArea) {
      return `${Number(valueArea * 5.04).toFixed(2)} руб.`;
    },
    formatToPriceAreaOld(valueArea) {
      return `${Number(valueArea * 3.89).toFixed(2)} руб.`;
    }
  }
};
</script>

<style scoped>
.calculatorBlock {
  margin-top: 32px;
}

h1 {
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 32px;
}

h2 {
  font-size: 17px;
  font-weight: bold;
  margin-bottom: 8px;
}

.calculatorLabel {
  margin-bottom: 8px;
  display: block;
}

.calculatorInput {
  font-size: 24px;
  padding: 5px;
  border: 1px solid #ccc;
}

.calculatorInput:focus {
  outline: 0;
  outline: none;
  border: 1px solid #1588E2;
}


.calculatorPrice {
  margin-top: 12px;
  font-size: 40px;
}

</style>



